import React, { useState, useEffect } from "react";
import { navigate } from "gatsby";

export default function Timer({ delayResend = "240", click, setClick, setError, setModal }){

    const windowGlobal = typeof window !== 'undefined' && window;

    const [delay, setDelay] = useState(+delayResend);
    const minutes = Math.floor(delay / 60);
    const seconds = Math.floor(delay % 60);

    useEffect(() => {
        const timer = setInterval(() => {
            setDelay(delay - 1);
        }, 1000);

        if ( delay <= 80 ){
            setClick(false);
            if(click === false){
                setClick(false);
                setError(10);
                setModal(true);
            } else if (click === true) {
                clearInterval(timer);
                console.log("Activo");
                setDelay(+delayResend); 
            }
        } 
        if(delay === 0){
            clearInterval(timer);
            navigate("/");
            var myItem = windowGlobal.localStorage.getItem('token');
            windowGlobal.localStorage.clear();
            windowGlobal.localStorage.setItem('token', myItem);
            console.log("Pasa por aquí, inicia un reedirect")
        }

        return () => {
            clearInterval(timer);
        };
    }, [delay, click]);

    return(
        <div >{/*Tengo un temporizador jiji {minutes} : {seconds}*/}</div>
    )

}